.aboutUsWrapper {
  margin-top: 20px;
  padding-left: 40px;
  padding-right: 40px;
  h2 {
    font-size: 48px;
    letter-spacing: 0px;
    line-height: 72px;
    text-transform: uppercase;
    color: #abbdd6;
    font-weight: 400;
    font-family: 'Philosopher';
    text-align: center;
  }

  .aboutUsBody {
    align-items: center;
    display: flex;
    .headerDecoration {
      width: 100px;
      height: 5px;
      background-color: #d8a27d;
      margin: 0 auto;
    }
    h3 {
      font-size: 30px;
      letter-spacing: 0px;
      line-height: 36px;
      text-transform: uppercase;
      color: #abbdd6;
      font-weight: 400;
      font-family: 'Philosopher';
      text-align: center;
    }
    .leftSideText,
    .rightSideText {
      font-size: 18px;
      letter-spacing: 0px;
      line-height: 22px;
      color: #383838;
      font-weight: 250;
      font-family: 'Raleway';
    }
  }
}
